import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'

Vue.use(Vuex)

const state = {
	latitude: '', //
	longitude: '', //
   	userInfo: null, //
   	geohash: 'wtw3sm0q087',//geohash
   	login: true,  //
   	imgPath: null, //
}

export default new Vuex.Store({
	state,
	mutations,
	actions
})



